// Article

main{
	@include u-padding-y(6);

	// Base Article Header Styles
	header.page-header,
	> header,
 	> article > header{
		@include u-padding-y(2);

		// Breadcrumbs
   	.breadcrumb{
     	@include u-margin-y(2);
     	@include u-text('bold');
     	@include u-font('sans', 'md');
     	a{
       	span{
         	@include u-padding-bottom('2px');
       	}
       	@include u-text('no-underline');
       	&:hover span{
         	border-bottom: 3px #656bd7 solid; // $indigo-40
       	}
     	}
   	}

		.kicker{
			@include u-margin(0);
			@include u-margin-bottom('05');
			@include u-font('sans', 'sm');
			@include u-text('semibold');
			@include u-text('uppercase');
			@include u-color('indigo-warm-60v');
		}
   	// Page Titles
   	h1{
     	@include u-margin(0);
     	@include u-margin-bottom('2px');
     	@include u-text('heavy');
     	@include u-line-height('sans', 2);
			@include at-media('tablet') {
				@include u-line-height('sans', 2);
			}
   	}

   	// Deck
   	.deck{
			@include u-margin(0);
     	@include u-font('sans', 'lg');
     	@include u-text('light');
     	@include u-text('indigo-warm-70');
			@include u-line-height('sans', 2);
			@include at-media('tablet') {
				@include u-font('sans', 'xl');
				@include u-line-height('sans', 2);
			}
   	}

		p{
			@include u-margin(0);
			@include u-font('sans', 'md');
		}

 	}


	// Base Article Styles
	article{
		@include u-margin-bottom(5);
		.date{
			@include u-margin-top(3);
			@include u-font('sans', '2xs');
			a{
				@include u-text('gray-cool-60');
				@include u-text('semibold');
				@include u-text('no-underline');
			}
			a:hover{
				border-bottom: 2px #8889db solid; // $indigo-40
			}
		}

	}
}
