.card{
	@include u-margin-bottom(1);
	@include u-bg('white');
	@include u-border-bottom('1px', 'gray-cool-10', 'solid');
	.paper{
		@include u-padding('105', !important);
		@include u-bg('white', !important);
		transition: background-color .2s;
		@include at-media('tablet') {
			@include u-padding('205', !important);
		}
	}
	.kicker{
		@include u-margin(0);
		@include u-margin-bottom('05');
		@include u-font('sans', '2xs');
		@include u-text('semibold');
		@include u-text('uppercase');
		@include u-color('indigo-warm-60v');
	}
	h3{
		@include u-margin(0);
		@include u-font('sans', 'lg');
		@include u-text('bold');
		a{
			@include u-text('indigo-warm-80');
			@include u-text('no-underline');
		}
	}
	p{
		@include u-margin-top(1);
		@include u-line-height('sans', 3);
		@include u-font('sans', 'md');
	}
	.deck{
		@include u-text('light');
	}

	.image-featured{
		@include u-margin-bottom(2);
	}

	.meta{
		@include u-margin-top(2);
		@include at-media('tablet') {
			@include u-margin-top(3);
			@include u-display('flex');
			@include u-flex('align-center');
		}
		.clicks{
			@include u-margin-top(0);
			@include u-margin-right(1);
			@include at-media('tablet'){
				@include u-margin-right(2);
			}
			@include u-font('sans', '2xs');
			@include u-text('italic');
			@include u-display('block');
			@include u-color('gold-40v');
			i{
				@include u-color('gold-30v');
			}
			white-space: nowrap;
		}
		.datetime{
			@include u-margin(0);
			@include at-media('tablet'){
				@include u-margin-right(2);
			}
			@include u-font('sans', '2xs');
			@include u-display('inline-block');
			@include u-color('gray-50');
			white-space: nowrap;
		}
		.authors-list-short{
			@include at-media('tablet') {
				@include u-order('last');
			}
		}
		.topics{
			@include u-margin-y(0);
			@include u-font('sans', '3xs');
		}
	}

	.entry-featured img {
		filter: grayscale(75%);
		transition: filter .2s;
	}

	&:hover{
		.entry-featured img {
			filter: grayscale(0%);
		}
	}

}
.card-linked{
	// To make the whole PROMO clickable, we put and additional <a> tag with a <span> tag in the markup that stretches to the full height and width of the element.
	@include u-position('relative');
	.overlay span{
		@include u-margin(0);
		@include u-position('absolute');
		@include u-width('full');
		@include u-height('full');
		@include u-top(0);
		@include u-left(0);
		cursor: pointer;
		z-index: 1;
	}
	&:hover{
		.paper{
			@include u-bg('gray-cool-2', !important);
		}
		h3 a{
			@include u-border-bottom('2px', 'orange-warm-30v', 'solid');
		}
		.play{
			@include u-bg('indigo-warm-60v');
		}
		.btn-register{
			@include u-bg('indigo-warm-60v');
			@include u-text('white');
		}
		.topics{
			a{
				@include u-bg('indigo-warm-20');
				&:hover{
					@include u-bg('indigo-warm-30');
				}
			}
		}
	}
}
